.card {
  width: 100%;
  position: relative;
  display: flex;
  flex-direction: column;
  margin-bottom: 30px;

  &:hover {
    box-shadow: 0 0 20px 10px rgb(206, 206, 206);

    .card__content {
      padding-left: 5px;
    }
  }

  @media (min-width: $tablet-width) {
    margin: 0;
  }

  &--main {
    box-sizing: border-box;
    padding: 20px;
    margin-top: 90px;
    background-color: #ffffff;

    &:hover {
      transform: none;

      .card__content {
       padding: 0;
      }
    }

    @media (min-width: $desktop-width) {
      flex-direction: row;
    }

    @media (min-width: 1600px) {
      position: relative;
    }
  }
}

.card__tag {
  color: $accent-color;
  font-size: 14px;
  margin-right: 40px;
  font-weight: 400;

  @media (min-width: $tablet-width) {
    font-size: 16px;
  }
}

.card__date {
  color: $light-color;
  font-size: 12px;
}

.card__tags {
  display: flex;
  align-items: baseline;
  flex-wrap: wrap;
}

.card__content {
  margin-top: 35px;
  display: flex;
  flex-direction: column;
  flex-grow: 1;

  &--main {

    @media (min-width: $desktop-width) {
      margin-left: 50px;
      margin-top: 0;
      display: flex;
    }

    @media (min-width: 1600px) {
      flex-direction: row;
      justify-content: space-between;
      max-width: 600px;
    }
  }
}

.card__pict {
  display: block;
  width: 100%;
  object-fit: contain;
  height: 100%;
}

.card__title {
    font-size: 16px;
    margin-top: 12px;
    font-weight: 600;

    @media (min-width: $tablet-width) {
      font-size: 20px;
    }

    &--main {
      margin-bottom: 30px;

      @media (min-width: $tablet-width) {
        margin-bottom: 50px;
      }

      @media (min-width: $tablet-width) {
        margin-bottom: 70px;
      }
    }
}

.card__arow {
  margin-top: auto;
  border: none;
  background-color: transparent;
  padding: 0;

  &--main {
    @media (min-width: 1600px) {
      position: absolute;
      bottom: 50px;
      right: 50px;
    }
  }
}

.card__icon {
  display: block;
}

.card__image {
  position: relative;

  &::after {
    content: '';
    width: 100%;
    height: 100%;
    background: linear-gradient(180deg, rgba(0, 0, 0, 0) 75.19%, rgba(0, 0, 0, 0.53) 100%);
    position: absolute;
    bottom: 0;
  }

  &--main {
    flex-shrink: 0;
    &::after {
      content: '';
      background: none;
    }
  }
}

.card__link {
  cursor: pointer;
  display: block;
  text-decoration: none;
  color: $main-color;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 1;

  &--main {
    display: block;
  }
}

.card__desc {
  margin-bottom: 35px;

  @media (min-width: $tablet-width) {
    margin-bottom: 46px;
  }
}

.card__text {
  font-weight: 300;
}