.footer {
  background-color: #ffffff;
  padding: 59px 0 49px;
  font-weight: 400;
}

.footer__slogan {
  font-size: 20px;
  margin: 0 0 18px 0;
  @media (min-width: $tablet-width) {
    font-size: 24px;
  }
}

.footer__social {
  border-bottom: 4px solid $main-color;
}

.footer__license {
  font-size: 14px;
  margin: 47px 0;

  @media (min-width: $tablet-width) {
    margin: 47px 0 0;
  }
}

.footer__bottom {
  display: flex;
  flex-direction: column;

  @media (min-width: $tablet-width) {
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-end;
  }
}

.footer__logo {
  &:hover {
    color: $accent-color;
  }

  &:active {
    color: $light-color;
  }
}